.contactListWrapper {
  margin: 0 auto;
  width: 650px;
  display: flex;
  max-height: 200px;
  justify-content: center;
 
}
.contactList{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  column-gap: 10px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
  max-height: calc(8*2rem);
  align-items: center;
  justify-content: center;
}
.contactListItem {
  margin-bottom: 5px;
  text-align: center;
}
.contactListBtn {
  margin-left: 10px;
  background-color: white;
  border-radius: 4px;
  border-width: 1px;
  cursor: pointer;
  &:hover,
  &:focus {
    background-color: rgb(197, 193, 193, 0.75);
  }
}
